import * as uuid from 'uuid';

import { BaseValueObject } from '../base';

class UuidV7<T extends string> extends BaseValueObject<T, string> {
  protected validate(value: string): string {
    if (uuid.validate(value) === false || uuid.version(value) !== 7) {
      throw new Error('Invalid UUID v7: ' + value);
    }
    return value;
  }
}

export class ProjectId extends UuidV7<'ProjectId'> {
  static from(value: string): ProjectId {
    return new ProjectId(value);
  }

  static make(): ProjectId {
    return new ProjectId(uuid.v7());
  }
}

